.recipe-manager-wrapper {
    display: grid;
    grid-template-columns: 3fr 1fr;
    
    .left-column {
        background-color: $offwhite;
    }

    .right-column {
        background-color: $charcoal;
    }

    @media screen and (max-width: 700px) {
        grid-template-columns: 1fr;
    }
}