* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  width: 100%;
  font-family: "Sofia Sans", sans-serif;
}

ul, ol {
  list-style-position: inside;
}

.link {
  text-decoration: none;
  color: inherit;
}

.cursive {
  font-family: 'Dancing Script', cursive;
}

.app {
  display: flex;
  justify-content: center;
  width: 100vw;
}

.app .container {
  display: flex;
  justify-content: center;
}

.auth-page-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100vh;
  width: 100vw;
}

.auth-page-wrapper .left-column {
  width: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.auth-page-wrapper .right-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #b9e7e7;
}

.auth-page-wrapper .right-column .logo {
  margin-bottom: 30px;
}

.auth-page-wrapper .right-column .logo img {
  height: 3rem;
  width: auto;
  border-radius: 5px;
  box-shadow: 0px 1px 22px -6px #26bfd4;
}

.auth-page-wrapper .right-column h1 {
  font-size: 20px;
  color: #207b88;
  margin-bottom: 20px;
}

.auth-page-wrapper .right-column p {
  font-size: 14px;
  margin-top: 10px;
  text-align: center;
}

.auth-page-wrapper .right-column span {
  margin-left: 10px;
  font-size: 16px;
  font-weight: 700;
  color: #207b88;
}

.auth-page-wrapper .right-column span:hover {
  text-decoration: underline;
  color: #333333;
}

.auth-page-wrapper .right-column .errMsg {
  color: #922a2a;
}

.auth-page-wrapper .right-column form {
  padding: 50px;
  background-color: #f6f6f6;
  border-radius: 6px;
  width: 80%;
  display: grid;
  grid-gap: 21px;
}

.auth-page-wrapper .right-column form input {
  border: 1px solid #eaeaea;
  height: 46px;
  padding: 10px 16px;
  font-size: 18px;
  line-height: 1.3rem;
  border-radius: 6px;
  box-shadow: 1px 1px rgba(0, 0, 0, 0.075);
  transition: 0.15s ease-in-out;
}

.auth-page-wrapper .right-column form input::placeholder {
  color: #c3c3c3;
}

.auth-page-wrapper .right-column form input:focus {
  outline: none;
  border: 1px solid #26bfd4;
  box-shadow: 0px 1px 22px -6px #26bfd4;
}

.auth-page-wrapper .right-column form button {
  width: fit-content;
  font-size: 18px;
  padding: 10px 25px;
  border: none;
  border-radius: 6px;
  background-color: #26bfd4;
  color: white;
  border-radius: 2px;
  cursor: pointer;
  transition: 0.15s;
}

.auth-page-wrapper .right-column form button:hover {
  background-color: #207b88;
}

@media screen and (max-width: 950px) {
  .auth-page-wrapper {
    grid-template-columns: 1fr;
    grid-gap: 0;
  }
  .auth-page-wrapper .left-column {
    height: auto;
  }
  .auth-page-wrapper .right-column .logo {
    margin-bottom: 15px;
  }
  .auth-page-wrapper .right-column .logo img {
    height: 2rem;
  }
  .auth-page-wrapper .right-column h1 {
    font-size: 16px;
    margin-bottom: 10px;
  }
  .auth-page-wrapper .right-column p {
    font-size: 12px;
    margin-top: 5px;
  }
  .auth-page-wrapper .right-column span {
    margin-left: 5px;
    font-size: 14px;
  }
  .auth-page-wrapper .right-column form {
    padding: 25px;
  }
  .auth-page-wrapper .right-column form input {
    height: 30px;
    padding: 8px 14px;
    font-size: 14px;
    line-height: 1rem;
  }
  .auth-page-wrapper .right-column form button {
    width: fit-content;
    font-size: 16px;
    padding: 8px 20px;
  }
}

.navbar {
  width: 100vw;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  box-shadow: 0.125rem 0.375rem rgba(0, 0, 0, 0.05);
}

.navbar .top-navbar {
  display: grid;
  grid-template-columns: auto 1fr;
  padding: 0;
}

.navbar .top-navbar .left-column {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar .top-navbar .left-column .toggle {
  display: none;
}

.navbar .top-navbar .left-column .logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar .top-navbar .left-column .logo img {
  height: 3rem;
  width: auto;
  border-radius: 6px;
  box-shadow: 0px 1px 22px -6px #26bfd4;
}

.navbar .top-navbar .right-column {
  display: flex;
  gap: 2.5rem;
  align-items: center;
  justify-content: flex-end;
  color: #26bfd4;
}

.navbar .top-navbar .right-column .write {
  background-color: white;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
  font-size: 1.5rem;
}

.navbar .top-navbar .right-column .write:hover {
  color: #207b88;
  background-color: white;
}

.navbar .top-navbar .right-column .user .username {
  margin-left: 5px;
}

.navbar .bottom-navbar {
  width: 100%;
  height: 2rem;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-top: 1.5rem;
}

.navbar .bottom-navbar .navbar-links {
  display: flex;
  align-items: flex-start;
  gap: 30px;
  border-bottom: transparent 2px solid;
}

.navbar .bottom-navbar .navbar-links .manager-group {
  display: flex;
  gap: 30px;
}

.navbar .bottom-navbar .navbar-links .nav-link-active {
  border-bottom: #26bfd4 2px solid;
}

.navbar .bottom-navbar .navbar-links .navbar-link h6 {
  color: #333333;
  font-size: 16px;
  font-weight: 700;
}

@media screen and (max-width: 700px) {
  .navbar {
    padding: 1rem;
  }
  .navbar .top-navbar {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  .navbar .top-navbar .left-column .toggle {
    display: block;
    background-color: transparent;
    border: none;
    font-size: 1rem;
    color: #333333;
    cursor: pointer;
    margin-right: 15px;
  }
  .navbar .top-navbar .left-column .logo img {
    height: 1.5rem;
  }
  .navbar .top-navbar .right-column {
    gap: 1rem;
    font-size: 0.8rem;
    justify-content: center;
  }
  .navbar .top-navbar .right-column .write {
    height: 25px;
    width: 25px;
    font-size: 1rem;
  }
  .navbar .bottom-navbar {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 20px;
    left: 0px;
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
  }
  .navbar .bottom-navbar.visible {
    opacity: 1;
    visibility: visible;
  }
  .navbar .bottom-navbar .navbar-links {
    flex-direction: column;
    align-items: start;
    background-color: #f6f6f6;
    padding: 0 20px;
    gap: 0px;
  }
  .navbar .bottom-navbar .navbar-links .manager-group {
    flex-direction: column;
    gap: 0;
  }
  .navbar .bottom-navbar .navbar-links .navbar-link {
    width: 100%;
    padding: 0.75rem 0;
    border-bottom: 1px solid #26bfd4;
  }
}

.footer {
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 30px;
  width: 100vw;
  background-color: #f6f6f6;
  padding-top: 30px;
  padding-bottom: 30px;
}

.footer .image img {
  height: 2rem;
}

.footer .link:hover {
  text-decoration: underline;
}

@media screen and (max-width: 700px) {
  .footer {
    gap: 0;
    justify-content: space-evenly;
  }
  .footer .image img {
    height: 1rem;
  }
  .footer .link {
    font-size: 0.75rem;
  }
  .footer .auth-name {
    font-size: 0.75rem;
  }
}

.home-container {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home-container .home-wrapper {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 30px;
  margin-bottom: 30px;
}

.home-container .home-wrapper .home {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.home-container .home-wrapper .home .categories {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
}

.home-container .home-wrapper .home .categories button {
  padding: 5px 10px;
  background-color: #b9e7e7;
  border: none;
  border-radius: 6px;
}

.home-container .home-wrapper .home .categories button:hover {
  background-color: #26bfd4;
}

.home-container .home-wrapper .home .categories button h6 {
  text-transform: uppercase;
}

@media screen and (max-width: 700px) {
  .home-container .home-wrapper .home .categories {
    gap: 10px;
  }
  .home-container .home-wrapper .home .categories button {
    padding: 3px 8px;
    font-size: 0.75rem;
  }
}

.home-container .home-wrapper .home .categories-title {
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  margin-bottom: 20px;
}

.home-container .home-wrapper .home .categories-title h5 span {
  font-size: 0.8em;
  margin-left: 5px;
  margin-right: 5px;
}

.home-container .home-wrapper .home .recipes {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 50px;
}

.home-container .home-wrapper .home .recipes .recipe {
  display: flex;
  align-items: center;
  gap: 100px;
}

.home-container .home-wrapper .home .recipes .recipe:nth-child(2n+1) {
  flex-direction: row-reverse;
}

.home-container .home-wrapper .home .recipes .recipe .img {
  flex: 2;
  border-radius: 25px;
}

.home-container .home-wrapper .home .recipes .recipe .img img {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 25px;
}

.home-container .home-wrapper .home .recipes .recipe .content {
  flex: 3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 15px;
}

.home-container .home-wrapper .home .recipes .recipe .content h6 {
  font-family: 'Dancing Script', cursive;
  font-size: 30px;
  margin-left: 30px;
}

.home-container .home-wrapper .home .recipes .recipe .content h1 {
  font-size: 40px;
  line-height: 48px;
  letter-spacing: 0px;
  color: #333333;
}

.home-container .home-wrapper .home .recipes .recipe .content .time-servings {
  display: flex;
  gap: 30px;
}

.home-container .home-wrapper .home .recipes .recipe .content .time-servings .difficulty {
  text-transform: uppercase;
}

.home-container .home-wrapper .home .recipes .recipe .content .author-date {
  display: flex;
  gap: 20px;
}

.home-container .home-wrapper .home .recipes .recipe .content .author-date p {
  font-size: 12px;
  line-height: 22px;
  letter-spacing: 0px;
}

.home-container .home-wrapper .home .recipes .recipe .content .author-date p span {
  font-weight: 700;
}

.home-container .home-wrapper .home .recipes .recipe .content .author-date .date p {
  border-left: 1px solid #555555;
  color: #555555;
  padding-left: 10px;
}

@media screen and (max-width: 700px) {
  .home-container .home-wrapper .home .recipes .recipe {
    flex-direction: column;
    gap: 15px;
    border: 2px solid #b9e7e7;
    border-radius: 25px;
    padding: 10px;
    box-shadow: 0px 1px 22px -6px #26bfd4;
  }
  .home-container .home-wrapper .home .recipes .recipe:nth-child(2n+1) {
    flex-direction: column;
  }
  .home-container .home-wrapper .home .recipes .recipe .img {
    padding-bottom: 0px;
    padding-left: 0px;
    background-color: white;
  }
  .home-container .home-wrapper .home .recipes .recipe .content h6 {
    font-size: 20px;
    text-align: center;
  }
  .home-container .home-wrapper .home .recipes .recipe .content h1 {
    font-size: 20px;
    line-height: 28px;
    text-align: center;
  }
  .home-container .home-wrapper .home .recipes .recipe .content .time-servings {
    justify-content: center;
    gap: 20px;
    font-size: 0.75rem;
  }
  .home-container .home-wrapper .home .recipes .recipe .content .author-date {
    gap: 15px;
    justify-content: center;
  }
  .home-container .home-wrapper .home .recipes .recipe .content .author-date .author {
    font-size: 0.75rem;
  }
  .home-container .home-wrapper .home .recipes .recipe .content .author-date p {
    font-size: 10px;
    line-height: 20px;
  }
}

