.about-page-wrapper {
  height: calc(100vh - 174.4px);
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.about-page-wrapper .about-page-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 60px;
  height: calc(100vh - 0px);
  width: 100vw;
}

.about-page-wrapper .about-page-container .about-left-column {
  width: 100%;
}

.about-page-wrapper .about-page-container .about-right-column {
  margin-top: 50px;
  margin-bottom: 50px;
  margin-right: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: justify;
  gap: 20px;
}

.about-page-wrapper .about-page-container .about-right-column ul {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.about-page-wrapper .about-page-container .about-right-column .about-link {
  color: inherit;
}

@media screen and (max-width: 700px) {
  .about-page-wrapper .about-page-container {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .about-page-wrapper .about-page-container .about-left-column {
