.dropdown {
  position: relative;
}

.dropdown-btn {
  display: flex;
  align-items: center;
  width: fit-content;
  background-color: white;
  border-radius: 0.5rem;
  cursor: pointer;
  font-family: "Sofia Sans", sans-serif;
  font-weight: 700;
}

.toggle-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.5rem;
}

.button-open {
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.dropdown-content {
  position: absolute;
  min-width: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  margin-top: 0.5rem;
  background-color: white;
  border-radius: 0.5rem;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  max-height: 40vh;
  overflow-y: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none;
  visibility: hidden;
  transform: translateY(-5%);
  transition: transform 150ms ease-in-out, opacity 100ms ease-in-out;
  pointer-events: none;
}

@media screen and (max-width: 700px) {
  .dropdown-content {
    padding: 0.25rem;
  }
}

.dropdown-content::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.content-open {
  visibility: visible;
  transform: translateY(0);
  pointer-events: all;
}

.dropdown-item {
  padding: 0.5rem;
  margin: 0.1rem;
  width: 100%;
  border-radius: 0.5rem;
  cursor: pointer;
}

.dropdown-item:hover {
  background-color: #f6f6f6;
}

