}

.carousel .slide_direction {
  display: flex;
  justify-content: space-between;
}

.carousel .slide_direction .left,
.carousel .slide_direction .right {
  display: flex;
  align-items: center;
  background-color: #b9e7e7;
  color: #333333;
  padding: 10px 8px 8px 13px;
  margin: 0 20px;
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 10px;
  height: 25px;
  width: 25px;
}

